app.factory('dataCacher', function() {
  var cachedData = null;
  function setData(data) {
    cachedData = data;
  }

  function getData() {
    return cachedData;
  }

  function clearData() {
    cachedData = null;
  }

  return {
    getData: getData,
    setData: setData,
    clearData: clearData
  };
});
